import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { createPage, base, high } from 'nc-lightapp-front';
import * as actions from '../action/index';
import * as events from '../event/index';
const { NCDiv } = base;
const { PrintOutput } = high;
//新的筛选
//import Org from '../../../../uapbd/refer/org/FinanceOrgTreeRef/index.js';
import './index.less';
//下载
import EntryConfigRef from '../../../refer/hpfrefs/HpfEntryConfigGridRef/index';

const gridid = 'head';
const searchid = 'search';
const pageId = 'M0Z10209_czncc';
const appcode = 'M0Z10209';
const printNodeKey = null;
const urls = {
    queryUrl: '/nccloud/hpf/doctor/DoctorQuery.do',
    pageQueryUrl: '/nccloud/hpf/doctor/DoctorQueryPageGridByPks.do',
    saveUrl: '/nccloud/hpf/doctor/DoctorSave.do',
    printUrl: '/nccloud/hpf/doctor/DoctorPrint.do'
}

class DoctorTable extends Component {

    constructor(props) {
        super(props);
        this.state = {
            OID: '',
            searchValue: '',
            checkValue: 'false',
            json: {},
            //新的筛选
            //******************** */
            // org: {
            //     value: undefined,
            //     queryCondition: () => {//组织权限
            //         return {
            //             TreeRefActionExt: 'nccloud.web.refer.sqlbuilder.PrimaryOrgSQLBuilder'
            //         };
            //     },
                onChange: (val) => {
                    this.state.org.value = val;
                    this.setState(this.state, () => {
                        this.props.search.setSearchValByField(searchid, 'pk_org', { display: this.state.org.value.refname, value: this.state.org.value.refpk });
                        actions['onSearchAfterEvent'].bind(this, this.props)();
                    });
                },
            //},
            //*********************/
            pk_org: ''
        }

        let callback = (json, status, inlt) => {
            if (status) {
                this.setState({ json, inlt }, () => {
                    actions['initTemplate'].call(this, this.props, actions['onloaddata'].bind(this));
                });
            }
        }
        this.props.MultiInit.getMultiLang({ moduleId: 'M0Z10209', domainName: 'hpf', callback })
    }

    componentDidUpdate() {
        let l_formstatus = this.props.editTable.getStatus(gridid);
        if (l_formstatus != 'add' && l_formstatus != "edit") {
            window.onbeforeunload = null;
        }
        else {
            window.onbeforeunload = () => {//编辑态关闭页签或浏览器的提示
                return '';
            };
        }
    }


    render() {
        const { editTable, button, search, BillHeadInfo } = this.props;
        const { createBillHeadInfo } = BillHeadInfo;
        const { createButtonApp } = button;
        const { NCCreateSearch } = search;
        const { createEditTable } = editTable;
        const tableState = this.props.editTable.getStatus(gridid);

        //下载
        const { modal } = this.props;
        let { createModal } = modal;

        return (
            <div className="nc-single-table">
                {/* 头部 header */}
                <NCDiv className="nc-singleTable-header-area" style={tableState == 'browse' ? {} : { border: 'none' }} areaCode={NCDiv.config.HEADER}>
                    {/* 标题 title */}
                    <div className="header-title-search-area">
                        {createBillHeadInfo(
                            {
                                title: this.state.json['M0Z10209-000018']/* 国际化处理： 国家地区*/,
                                backBtnClick: () => { },
                                initShowBackBtn: false
                            }
                        )}
                    </div>
                    {/* 新的筛选*/}
                    {/* <div className="search-box">
                        {Org({
                            ...this.state.org,
                            disabled: this.state.editMode,
                            fieldid: 'org_fieldid'
                        })}
                    </div> */}
                  
                    {/* 按钮区  btn-group */}
                    <div className="header-button-area header-button-area-print-btn">
                        {createButtonApp({
                            area: 'header',//按钮注册中的按钮区域
                            onButtonClick: actions['onClickButton'].bind(this)
                        })}
                    </div>
                   
                </NCDiv>
                
                <div className="nc-singleTable-search-area" style={{ display: tableState == 'browse' ? '' : 'none' }}>
                        {NCCreateSearch(searchid, {//查询区
                            showAdvBtn: false,
                            showClearBtn: false,
                            showSearchBtn: false,
                            hideSearchCondition: true,
                            onAfterEvent: actions['onSearchAfterEvent'].bind(this, this.props)
                        })}
                    </div>

                {/* 列表区 */}
                <div className="nc-singleTable-table-area">
                    {createEditTable(gridid, {
                        handlePageInfoChange: actions['onClickPageInfo'].bind(this),
                        onCloseModel: events['onCloseTableModel'].bind(this),
                        onAfterEvent: events['onTableAfterEdit'].bind(this),
                        statusChange: actions['gridStatusChange'].bind(this),
                        selectedChange: actions['gridBeChecked'].bind(this),
                        onSelected: actions['onSelected'].bind(this),
                        onSelectedAll: actions['onSelected'].bind(this),
                        showCheck: true,
                        showIndex: true,
                        isAddRow: true,
                        showPagination: true,
                        adaptionHeight: true
                    })}
                </div>
                <PrintOutput
                    ref='printOutput'
                    url={urls['printUrl']}
                    data={{
                        funcode: appcode,//功能节点编码
                        nodekey: printNodeKey,//模板节点编码
                        oids: this.state.ids,
                        outputType: 'output'
                    }}
                />

                {
                    createModal('selds', {
                        title: '选择来源系统',
                        className: 'selds',
                        content:
                            <div>
                                <EntryConfigRef
                                    value={this.state.dbinfo}
                                    isMultiSelectedEnabled={false}
                                    queryCondition={v => {
                                        return {
                                            pk_org: this.state.pk_org
                                        }
                                    }}
                                    onChange={(v) => {
                                        this.setState({
                                            dbinfo: v,
                                        })
                                    }}
                                />
                            </div>
                        ,
                        beSureBtnClick: actions['beSureBtnClick'].bind(this, this.props),
                        cancelBtnClick: actions['cancelBtnClick'].bind(this),
                        size: 'sm'
                    })
                }

            </div>
        )
    }
}

DoctorTable = createPage({
    billinfo: [{
        billtype: 'grid',
        pagecode: pageId,
        bodycode: gridid
    }],
    mutiLangCode: appcode
})(DoctorTable);

ReactDOM.render(<DoctorTable />, document.querySelector('#app'));
